import Vue from 'vue'
import Router from 'vue-router'

import CategoriesScreen from './screens/0-CategoriesScreen.vue'
import CategoryScreen from './screens/1-CategoryScreen.vue'
import PhotoScreen from './screens/2-PhotoScreen.vue'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/',                          name: 'index',    component: CategoriesScreen, meta: { showBanner: false } },
    { path: '/category/:category',        name: 'category', component: CategoryScreen,   meta: { showBanner: true } },
    { path: '/category/:category/:photo', name: 'photo',    component: PhotoScreen,      meta: { showBanner: true } },
  ],
  // scrollBehavior (to, from, savedPosition) {
  scrollBehavior () {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({ x: 0, y: 0 })
      }, 200)
    })
  }  
})
