<template>
  <div class="image"
    :style="style"
  >
    <div class="loader-wrap"
      v-if="!loaded"
    >
      <div class="loader"></div>
    </div>
    <div class="hidden"
      ref="loadingContainer"
    ></div>
  </div>
</template>

<script>
var image = null

export default {
  props: {
    src: String,
    size: {
      type: String,
      default: 'cover',
    }
  },
  data() {
    return {
      loaded: false
    }
  },
  computed: {
    style() {
      return `background-size: ${this.size};` + this.loaded ? ` background-image: url("${ this.src }");` : ''
    }
  },
  mounted() {
    image = new Image()
    image.onload = () => {
      this.loaded = true
    }
    image.src = this.src
    // this.$refs.loadingContainer.appendChild(image)
  },
  beforeDestroy() {
    if (image) {
      image.src = ''
      image = null
    }
  }
}
</script>

<style scoped>
.loader-wrap {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.loader {
  width: 50px;
  height: 50px;
}
.image {
  width: 100%;
  height: 100%;
  background-position: center;
}
.hidden {
  overflow: hidden;
  height: 0;
}
</style>