let hostURL = 'https://qutties.com/', //process.env.BASE_URL
    formatPhotoId = (id) => (Number(id) + 1).toString(), //.padStart(2, '0'),
    getPhotoURL = (categoryId, photoId, quality = 'preview') => hostURL + 'imgs/' + categoryId + '/' + quality + '-' + formatPhotoId(photoId) + '.jpg',
    getCategoryCoverURL = (category) => getPhotoURL(category.id, category.coverId - 1)

export default {
  formatPhotoId,
  getPhotoURL,
  getCategoryCoverURL
}