<template>
  <div class="category">
    <router-link class="item"
      v-for="(photoURL, index) in photoURLs"
      :key="index"
      :to="{ name: 'photo', params: { category: category.id, photo: index }}"
    >
      <image-loader
        :src="photoURL"
      ></image-loader>
    </router-link>
  </div>
</template>

<script>
import pathHelper from '../helpers/pathHelper'

import ImageLoader from '../components/ImageLoader'

export default {
  components: {
    ImageLoader
  },
  data() {
    return {
      category: this.$store.getters.category(this.$route.params.category)
    }
  },
  computed: {
    photoURLs() {
      let urls = []
      for (let index = 0; index < this.category.count; index++) {
        urls.push(pathHelper.getPhotoURL(this.category.id, index))
      }
      return urls
    }
  },
}
</script>

<style scoped>
.item {
  float: left;
  position: relative;
  height: 30vh;
  width: 50%;
  background-size: cover;
  background-position: center;
}
</style>
