<template>
  <div>
    <router-link class="category"
      v-for="(category, index) in categories"
      :key="index"
      :to="{ name: 'category', params: { category: category.id }}"
    >
      <image-loader
        :src="getCategoryImageSrc(category)"
      ></image-loader>
      <div class="fader"
      ></div>
      <p class="name">{{ category.name }}</p>
    </router-link>
  </div>
</template>

<script>
import pathHelper from '../helpers/pathHelper'

import ImageLoader from '../components/ImageLoader'

export default {
  components: {
    ImageLoader
  },
  data() {
    return {
      categories: this.$store.getters.categories,
    }
  },
  methods: {
    getCategoryImageSrc(category) {
      return pathHelper.getCategoryCoverURL(category)
    }
  }
}
</script>

<style scoped>
.category {
  width: 100vw;
  height: 30vh;
  position: relative;
  display: block;
  background-position: 50% 50%;
  background-size: cover;
}
.fader {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, .3);
}
.name {
  color: #fff;
  font-weight: bold;
  font-size: 40px;
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
</style>
