import Vue from 'vue'
import Vuex from 'vuex'
// import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    ready: false,
    // appDataURL: 'https://photos.crypto-pie.com/data.json',
    appData: null,
    inviteURL: 'https://google.com/'
  },
  mutations: {
    areWeReady: state => state.ready = !!state.appData,
    appData: (state, data) => state.appData = data,
  },
  actions: {
    loadData({ state, commit }) {
      /*
      axios.get(state.appDataURL)
        .then(response => {
          commit('appData', response.data)
          commit('areWeReady')
        })
        // eslint-disable-next-line
        .catch(error => {
          // TODO check internet connection
        })
      */
      commit('appData', window.appData.data)
      commit('inviteURL', window.appData.url)
      commit('areWeReady')
    },
  },
  getters: {
    ready: state => state.ready,
    categories: state => state.appData.categories,
    category: (state, getters) => id => getters.categories.find(category => category.id == id),
  }
})
