              c-20.486-5.532-43.278-0.274-59.358,15.807l0,0c-23.931,23.931-23.931,62.731,0,86.663l99.973,99.973
              c3.223,3.223,8.45,3.223,11.673,0l10.19-10.192L182.145,209.204z"/>
          </svg>
        </div>
        <div class="media-content">
          <p>
            You have a new message from {{ category.name }}!
          </p>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import javaBridge from '../helpers/javaBridge'
import pathHelper from '../helpers/pathHelper'

import ImageLoader from '../components/ImageLoader'

export default {
  components: {
    ImageLoader
  },
  data() {
    let categoryId = this.$route.params.category
    let photoIndex = this.$route.params.photo
    let category = this.$store.getters.category(categoryId)
    return {
      photoIndex,
      category,
      photoURL: pathHelper.getPhotoURL(categoryId, photoIndex, 'full'),
      showInvite: false,
      dragStartX: 0,
      dragFinishX: 0,
      dragStarted: false,
    }
  },
  mounted() {
    setTimeout(() => {
      this.showInvite = true
    }, 2000)
  },
  methods: {
    onMouseMove(e) {
      if (e.buttons !== 0) {
        if (!this.dragStarted) {
          this.dragStart(e)
        } else {
          this.dragUpdate(e)
        }
      } else if (this.dragStarted) {
        this.dragEnd(e)
      }
    },
    dragStart(e) {
      this.dragStarted = true
      this.dragStartX = e.clientX || e.touches[0].clientX
    },
    dragUpdate(e) {
      this.dragFinishX = e.clientX || e.touches[0].clientX
    },
    dragEnd() {
      this.dragStarted = false
      let deltaX = this.dragStartX - this.dragFinishX
      if (Math.abs(deltaX) > 50) {
        let direction = Math.sign(deltaX)
        let nextPhoto = (this.$route.params.photo + direction + this.category.count) % this.category.count
        this.$router.replace({
          name: 'photo',
          params: {
            category: this.category.id,
            photo: nextPhoto,
          }
        })
      }
    },
    onInviteClick() {
      javaBridge.openURL(this.$store.state.inviteURL)
    }
  }
}
</script>

<style>
.slide-up-fade-enter-active, .slide-up-fade-leave-active {
  transition: opacity .2s ease-in-out,
              transform .2s ease-in-out;
}
.slide-up-fade-enter, .slide-up-fade-leave-to {
  transform: translateY(100%);
  opacity: 0;
}
</style>


<style scoped>
.icon {
  width: 64px;
  height: 64px;
}
.photo {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: center;
}
.invite {
  position: fixed;
  background: #fff;
  border-radius: 5px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  padding: 20px 20px 12px;
}
</style>
